import React, { Component } from 'react';
import { Row, Col, Form } from 'antd';
import { withRouter } from 'react-router';
import { getTrainTypes } from 'services/trainType';
import { SelectAjax } from 'components/common/index';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'FormSearchEnrollStudyField' })
class SearchBar extends Component {
  state = {};

  formItemLayout = 6;

  handleSearch = e => {
    if(e) {
      e.preventDefault();
    }
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.onSearch(values);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    this.handleSearch();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Hệ đào tạo">
              {getFieldDecorator('trainTypeId')(
                <SelectAjax service={getTrainTypes} />
              )}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default withRouter(SearchBar);



