import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getEnrollStudyFields, deleteEnrollStudyField } from 'services/enrollStudyField';
import SearchBar from './SearchBar';
import './style.scss';

class List extends Component {
  state = {
    reload: 0,
    searchParams: new Date().getFullYear(),
  };

  onSearch = searchParams => {
    console.log(searchParams);
    this.setState({ searchParams, reload: this.state.reload + 1 });
  };

  handleDelete = async id => {
    await deleteEnrollStudyField(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Mã ngành',
        dataIndex: 'id',
        key: 'id',
        align: 'center',
      },
      {
        title: 'Tên',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hệ đào tạo',
        dataIndex: 'trainTypeName',
        key: 'trainTypeName',
        align: 'center',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TUYEN_SINH__MA_NGANH__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    console.log(this.state.searchParams);
    return (
      <Card title="Ngành tuyển dụng" className="enroll-study-field">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__TUYEN_SINH__MA_NGANH__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <SearchBar onSearch={this.onSearch} />
          </Col>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.searchParams}
              service={getEnrollStudyFields}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <List />
  </Page>
);



